import React from 'react';
import { Table } from 'antd';
import './DrawerTable'

const columns = [
  {
    title: ' ',
    dataIndex: 'source_name_humanize',
    key:'source_name_humanize'
  },
  {
    title: '',
    dataIndex: 'point',
    align: 'center',
    key: 'point',
    defaultSortOrder: "descend",
    sortDirections: ["descend", "ascend", "descend"],
    sorter: (a, b) => a.point - b.point
  },
];



const DrawerTable = (props) => <Table className='ad' size='small' pagination={false} columns={columns} dataSource={props.data} rowKey={item => item.id}/>;
export default DrawerTable;