import React from "react";
import { DatePicker, Space } from "antd";
import dayjs from "dayjs";
const updateLocale = require("dayjs/plugin/updateLocale");

const months = [
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
];

dayjs.extend(updateLocale);
dayjs.updateLocale("en", {
  months: months,
  weekdays: [
    "",
    "",
    "",
    "",
    "",
    "",
    "",
  ],
  weekdaysMin: ["", "", "", "", "", "", ""],
  weekStart: 1,
});

const rangePresets = [
  {
    label: " ",
    value: dayjs(),
  },
  {
    label: " ",
    value: dayjs().add(-1, "w"),
  },
  {
    label: "2  ",
    value: dayjs().add(-2, "w"),
  },
  {
    label: "3  ",
    value: dayjs().add(-3, "w"),
  },
  {
    label: "4  ",
    value: dayjs().add(-4, "w"),
  },
  {
    label: "5  ",
    value: dayjs().add(-5, "w"),
  },
  {
    label: "6  ",
    value: dayjs().add(-6, "w"),
  },
];

const MainDatePicker = (props) => {
  const onChange = (date, dateString) => {
    props.getDate([date.startOf("week"), date.endOf("week")]);
  };

  let current = dayjs().week().toString() + " ";

  if (props.timeFilter.length !== 0) {
    current = dayjs.unix(props.timeFilter[0]).week().toString() + " ";
  }
  return (
    <Space
      direction="horizontal"
      size={10}
      align="end"
      style={{ float: "right" }}
    >
      <DatePicker
        onChange={onChange}
        presets={rangePresets}
        placeholder={current}
        picker="week"
      />
    </Space>
  );
};
export default MainDatePicker;
