import React from "react";
import { useState, useEffect } from "react";
import { Table } from "antd";
import dayjs from "dayjs";
import Loading from "./Loading";

const columns = [
  {
    title: "",
    dataIndex: "id",
    key: "id",
    render: (text, record, index) => index + 1,
    width: "10%",
    align: "center",
  },
  {
    title: "",
    dataIndex: "received",
    key: "received",
    render: (text, record) =>
      dayjs(record["received"]).format("DD.MM.YYYY HH:mm"),
    align: "center",
    width: "20%",
    sorter: (a, b) =>
      parseInt(dayjs(a.received).unix()) - parseInt(dayjs(b.received).unix()),
    defaultSortOrder: "descend",
    sortDirections: ["descend", "ascend", "descend"],
  },
  {
    title: "",
    dataIndex: "source_name",
    key: "source_name",
    align: "center",
  },
  {
    title: "",
    dataIndex: "chest_name",
    key: "chest_name",
    align: "center",
  },
  {
    title: "",
    dataIndex: "points",
    key: "points",
    width: "10%",
    align: "center",
    sorter: (a, b) => a.points - b.points,
    sortDirections: ["descend", "ascend", "descend"],
  },
];

const ModalTable = (props) => {
  const [gifts, setGifts] = useState([]);
  const [loading, setLoading] = useState(true);

  let url = "";

  let timeFilter = props.timeFilter;
  if (timeFilter[0] === undefined) {
    url = `http://81.200.148.175/api/v1/giftbyplayer/${props.userId}/`;
  } else {
    url = `http://81.200.148.175/api/v1/giftbyplayer/${props.userId}/?start=${timeFilter[0]}&end=${timeFilter[1]}`;
  }

  useEffect(() => {
    const getGifts = async () => {
      let response = await fetch(url);
      let json = await response.json();
      setGifts(json);
      setLoading(false);
    };
    getGifts();
  }, [timeFilter]);

  return (
    <div>
      {loading ? (
        <Loading key="main_loading" />
      ) : (
        <Table
          pagination={false}
          showSorterTooltip={false}
          columns={columns}
          dataSource={gifts}
          scroll={{ y: "60vh", x: "max-content" }}
          size={"small"}
          bordered
          locale={{ emptyText: "" }}
          rowKey={(record) => Math.random(500)}
        />
      )}
    </div>
  );
};

export default ModalTable;
