import React, { useState } from 'react';
import { Button, Drawer } from 'antd';
import DrawerTable from './DrawerTable';

const DrawerPoints = (props) => {
  const [open, setOpen] = useState(false);
  const [placement] = useState('left');
  const showDrawer = () => {
    setOpen(true);
  };

  const onClose = () => {
    setOpen(false);
  };

  return (
    <>
        <Button key="main_button" type="primary" onClick={showDrawer} style={{marginRight: '10px'}}>
            
        </Button>
      <Drawer
        title="  "
        placement={placement}
        closable={false}
        onClose={onClose}
        open={open}
        key={placement}
      >
        <DrawerTable data={props.points} key="drawer_table" />
      </Drawer>
    </>
  );
};
export default DrawerPoints;