import React from "react";
import { Table, Typography } from "antd";
import UserModal from "./UserModal";

const MainTable = (props) => {
  const col_width = 60;

  let data = props.data;

  let dataSource = [];

  for (const [key, value] of Object.entries(data)) {
    let new_dict = value;
    new_dict["id"] = key;
    dataSource.push(new_dict);
  }

  function clean(obj) {
    return Object.keys(obj)
      .filter(function (key) {
        if (
          [
            "cita_credit",
            "crypt_credit",
            "all_credit",
            "all_points_rate",
            "level",
            "cita_points",
            "crypt_points",
            "gift_count",
          ].includes(key)
        ) {
          return true;
        } else {
          return obj[key] !== 0;
        }
      })
      .reduce(function (out, key) {
        out[key] = obj[key];
        return out;
      }, {});
  }

  dataSource = dataSource.filter((item) => item["hdn"] === false);

  dataSource.forEach((item) => {
    item["bank"] = 0;
    item["heroic_monster"] = 0;
    item["20_citadel"] = 0;
    item["25_citadel"] = 0;
    for (const [key, value] of Object.entries(item)) {
      if (!key.includes("others")) {
        item["others"] = 0;
      } else {
        item["others"] = value;
      }
    }
    for (const [key, value] of Object.entries(item)) {
      if (key.includes("bank")) {
        item["bank"] += value;
      } else if (key.includes("heroic_monster") || key.includes("ancient") || key.includes("epic_monster")) {
        item["heroic_monster"] += value;
      } else if (key.includes("20_citadel")) {
        item["20_citadel"] += value;
      } else if (key.includes("25_citadel")) {
        item["25_citadel"] += value;
      } else if (
        key.includes("mercenary_exchange") ||
        key.includes("authority_rush_tm") ||
        key.includes("festival")
      ) {
        item["others"] += value;
      }
    }
  });

  console.log(dataSource);

  dataSource = dataSource.map(clean);

  let columns_dict = {
    columns2: [
      {
        title: "",
        dataIndex: "player_name",
        key: "player_name",
        align: "center",
        width: 10,
        minWidth: 10,
        sortDirections: ["ascend", "descend", "ascend"],
        render: (text, record) => (
          <Typography.Text ellipsis>
            <UserModal username={record} timeFilter={props.timeFilter} />
          </Typography.Text>
        ),
        sorter: {
          compare: (a, b) => a["player_name"].localeCompare(b["player_name"]),
        },
      },
      {
        title: " ",
        dataIndex: "level",
        key: "level",
        align: "center",
        width: 90,
        minWidth: 90,
        defaultSortOrder: "descend",
        sortDirections: ["ascend", "descend", "ascend"],
        sorter: (a, b) => a.level - b.level,
      },
      {
        title: " ",
        children: [
          {
            title: "",
            dataIndex: "cita_credit",
            key: "cita_credit",
            align: "center",
            width: 90,
          },
          {
            title: "",
            dataIndex: "crypt_credit",
            key: "crypt_credit",
            align: "center",
            width: 90,
          },
          {
            title: "",
            dataIndex: "all_credit",
            key: "all_credit",
            align: "center",
            width: 90,
          },
        ],
      },
      {
        title: "",
        dataIndex: "all_points_rate",
        key: "all_points_rate",
        align: "center",
        width: 120,
        minWidth: 120,
        render(text, record) {
          let colorBG = "";
          let cita = record.cita_points_rate - record.cita_points;
          let debt = record.cita_points_rate - record.cita_points;
          if (debt > 0) {
            debt = 0;
          }
          let crypt =
            record.crypt_points_rate - record.crypt_points - Math.abs(debt);
          if (cita < 0 && crypt < 0) {
            colorBG = "#c8fac3";
          }
          return {
            props: {
              style: { background: colorBG },
            },
            children: <div>{text}</div>,
          };
        },
      },
      {
        title: "  ",
        dataIndex: "cita_points",
        key: "cita_points",
        align: "center",
        width: 100,
        minWidth: 100,
        render(text, record) {
          let colorBG = "";
          if (record.cita_points_rate - record.cita_points < 0) {
            colorBG = "#c8fac3";
          } else {
            colorBG = "#fad9d9";
          }
          return {
            props: {
              style: { background: colorBG },
            },
            children: <div>{text}</div>,
          };
        },
      },
      {
        title: "  ",
        dataIndex: "crypt_points",
        key: "crypt_points",
        align: "center",
        width: 100,
        minWidth: 100,
        render(text, record) {
          let colorBG = "";
          let debt = record.cita_points_rate - record.cita_points;
          if (debt > 0) {
            debt = 0;
          }
          if (
            record.crypt_points_rate - record.crypt_points - Math.abs(debt) <
            0
          ) {
            colorBG = "#c8fac3";
          } else {
            colorBG = "#fad9d9";
          }
          return {
            props: {
              style: { background: colorBG },
            },
            children: <div>{text}</div>,
          };
        },
      },
      {
        title: " ",
        dataIndex: "gift_count",
        key: "gift_count",
        align: "center",
        width: 100,
        minWidth: 100,
      },
      {
        title: "",
        children: [
          {
            title: "10",
            dataIndex: "10_citadel",
            key: "10_citadel",
            align: "center",
            width: col_width,
          },
          {
            title: "15",
            dataIndex: "15_citadel",
            key: "15_citadel",
            align: "center",
            width: col_width,
          },
          {
            title: "20",
            dataIndex: "20_citadel",
            key: "20_citadel",
            align: "center",
            width: col_width,
          },
          {
            title: "25",
            dataIndex: "25_citadel",
            key: "25_citadel",
            align: "center",
            width: col_width,
          },
          {
            title: "30",
            dataIndex: "30_citadel",
            key: "30_citadel",
            align: "center",
            width: col_width,
          },
        ],
      },
      {
        title: "",
        children: [
          {
            title: "5",
            dataIndex: "5_crypt",
            key: "5_crypt",
            align: "center",
            width: col_width,
          },
          {
            title: "10",
            dataIndex: "10_crypt",
            key: "10_crypt",
            align: "center",
            width: col_width,
          },
          {
            title: "15",
            dataIndex: "15_crypt",
            key: "15_crypt",
            align: "center",
            width: col_width,
          },
          {
            title: "20",
            dataIndex: "20_crypt",
            key: "20_crypt",
            align: "center",
            width: col_width,
          },
          {
            title: "25",
            dataIndex: "25_crypt",
            key: "25_crypt",
            align: "center",
            width: col_width,
          },
        ],
      },
      {
        title: " ",
        children: [
          {
            title: "15",
            dataIndex: "15_epic_crypt",
            key: "15_epic_crypt",
            align: "center",
            width: col_width,
          },
          {
            title: "20",
            dataIndex: "20_epic_crypt",
            key: "20_epic_crypt",
            align: "center",
            width: col_width,
          },
          {
            title: "25",
            dataIndex: "25_epic_crypt",
            key: "25_epic_crypt",
            align: "center",
            width: col_width,
          },
          {
            title: "30",
            dataIndex: "30_epic_crypt",
            key: "30_epic_crypt",
            align: "center",
            width: col_width,
          },
          {
            title: "35",
            dataIndex: "35_epic_crypt",
            key: "35_epic_crypt",
            align: "center",
            width: col_width,
          },
        ],
      },
      {
        title: " ",
        children: [
          {
            title: "10",
            dataIndex: "10_rare_crypt",
            key: "10_rare_crypt",
            align: "center",
            width: col_width,
          },
          {
            title: "15",
            dataIndex: "15_rare_crypt",
            key: "15_rare_crypt",
            align: "center",
            width: col_width,
          },
          {
            title: "20",
            dataIndex: "20_rare_crypt",
            key: "20_rare_crypt",
            align: "center",
            width: col_width,
          },
          {
            title: "25",
            dataIndex: "25_rare_crypt",
            key: "25_rare_crypt",
            align: "center",
            width: col_width,
          },
          {
            title: "30",
            dataIndex: "30_rare_crypt",
            key: "30_rare_crypt",
            align: "center",
            width: col_width,
          },
        ],
      },
      {
        title: "",
        dataIndex: "bank",
        key: "bank",
        align: "center",
        width: 70,
      },
      {
        title: "   ",
        dataIndex: "heroic_monster",
        key: "heroic_monster",
        align: "center",
        width: 120,
      },
      {
        title: "",
        dataIndex: "others",
        key: "others",
        align: "center",
        width: 90,
      },
    ],
  };

  const columns = columns_dict["columns2"];

  const onChange = (pagination, filters, sorter, extra) => {
    return false;
  };

  let cols = [];
  columns.forEach((col) => {
    if ("children" in col) {
      col["children"].map((item) => cols.push(item));
    }
    cols.push(col);
  });

  let dictTotal = {};
  dataSource.forEach((item) => {
    Object.entries(item).forEach((i) => {
      dictTotal[i[0]] = (dictTotal[i[0]] ?? 0) + i[1];
    });
  });

  cols.forEach((item, idx) => {
    if ("children" in item) {
      cols.splice(idx, 1);
    }
  });

  cols.forEach((item) => {
    if (!(item["dataIndex"] in dictTotal)) {
      dictTotal[item["dataIndex"]] = 0;
    }
  });

  dictTotal["player_name"] = "";
  dictTotal["level"] = "";
  dictTotal["cita_credit"] = "";
  dictTotal["crypt_credit"] = "";
  dictTotal["all_credit"] = "";
  dictTotal["all_points_rate"] = "";
  dictTotal["cita_points"] = "";
  dictTotal["crypt_points"] = "";

  const tableProps = {
    bordered: true,
    pagination: false,
    showSorterTooltip: false,
  };

  return (
    <Table
      className="modTable"
      {...tableProps}
      key="51515151"
      size="small"
      scroll={{ y: "71vh", scrollToFirstRowOnChange: true, x: 1850 }}
      columns={columns_dict[props.columns]}
      dataSource={dataSource}
      onChange={onChange}
      locale={{ emptyText: " " }}
      rowKey={(record) => record.id}
      tableLayout="auto"
      showSorterTooltip={false}
      summary={() => {
        return (
          <Table.Summary key={Math.random(2000)} fixed="top">
            <Table.Summary.Row style={{ backgroundColor: "#fafafa" }}>
              {cols.map((c, idx) => {
                return (
                  <Table.Summary.Cell key={idx} align="center">
                    {dictTotal[c["dataIndex"]]}
                  </Table.Summary.Cell>
                );
              })}
            </Table.Summary.Row>
          </Table.Summary>
        );
      }}
    >
      {columns.map((column) => (
        <Table.Column key={Math.random(2000)} {...column} />
      ))}
    </Table>
  );
};
export default MainTable;
