import React, { useState } from "react";
import { Button, Modal, Typography } from "antd";
import ModalTable from "./ModalTable";
import dayjs from "dayjs";

const UserModal = (props) => {
  const [open, setOpen] = useState(false);
  let userId = props.username["id"];

  const msg = "   " + props.username.player_name;
  return (
    <>
      <Button
        style={{ fontWeight: 700, color: "#1890ff" }}
        type="link"
        onClick={() => setOpen(true)}
      >
        {props.username.player_name}
      </Button>
      <Modal
        title={msg}
        centered
        open={open}
        width={"70%"}
        closable={false}
        cancelButtonProps={{ style: { display: "none" } }}
        okButtonProps={{ style: { display: "none" } }}
        maskClosable={true}
        onOk={() => setOpen(false)}
        onCancel={() => setOpen(false)}
      >
        <div style={{ marginBottom: "20px" }}>
          <Typography.Text>
              :{" "}
            {dayjs(props.username.joined_at, "YYYY-MM-DD").format("DD.MM.YYYY")}
          </Typography.Text>
        </div>
        <ModalTable userId={userId} timeFilter={props.timeFilter} />
      </Modal>
    </>
  );
};
export default UserModal;
