import "./App.css";
import { useState, useEffect } from "react";
import MainTable from "./components/MainTable";
import MainDatePicker from "./components/MainDatePicker";
import dayjs from "dayjs";
import Loading from "./components/Loading";
import DrawerPoints from "./components/DrawerPoints";
import { Typography, Space } from "antd";
const { Title } = Typography;
var utc = require("dayjs/plugin/utc");
var timezone = require("dayjs/plugin/timezone");

dayjs.extend(utc);
dayjs.extend(timezone);

function App() {
  const [users, setUsers] = useState({});
  const [points, setPoints] = useState([]);
  const [clanName, setClanName] = useState("");
  const [lastGift, setLastGift] = useState([]);

  const [loading, setLoading] = useState(true);

  const [timeFilter, setTimeFilter] = useState([]);

  let url = "";
  const getDate = (datePickerDate) => {
    let datetime = [
      datePickerDate[0].startOf("day").unix(),
      datePickerDate[1].endOf("day").unix(),
    ];
    setTimeFilter(datetime);
  };

  if (timeFilter[0] === undefined) {
    url = "http://81.200.148.175/api/v1/main/";
  } else {
    url = `http://81.200.148.175/api/v1/main/?start=${timeFilter[0]}&end=${timeFilter[1]}`;
  }
  useEffect(() => {
    const getGifts = async () => {
      setLoading(true);
      let response = await fetch(url);
      let json = await response.json();
      setClanName(json["clan_name"]);
      setPoints(json["points"]);
      setUsers(json["players"]);
      setLastGift(json["last_gift"]);
      setLoading(false);
    };
    getGifts();
  }, [timeFilter]);

  return (
    <div>
      {loading ? (
        <Loading key="main_loading" />
      ) : (
        <>
          <Title
            key="main_title"
            style={{ textAlign: "center", fontWeight: 700 }}
          >
            <a style={{ color: "#000000" }} href="/">
              {clanName}
            </a>
          </Title>
          <div style={{ margin: "25px 0" }}>
            <DrawerPoints points={points} />
            <span
              key="main_information"
              level={5}
              style={{ fontFamily: "Segoe UI" }}
            >
                   {lastGift}
            </span>
            <Space
              direction="horizontal"
              size={10}
              align="end"
              style={{ float: "right", display: "flex", alignItems: "center" }}
            >
              <MainDatePicker
                key="datePicker"
                getDate={getDate}
                timeFilter={timeFilter}
              />
            </Space>
          </div>
          <MainTable
            key="mainTable"
            data={users}
            url={url}
            category={points}
            columns={"columns2"}
            timeFilter={timeFilter}
          />
        </>
      )}
    </div>
  );
}

export default App;
